<template lang="html">
<!-- About Me Subpage -->
<section class="pt-page pt-page-2" data-id="about_me">
  <div class="border-block-top-110"></div>
  <div class="section-inner">
    <div class="section-title-block">
      <div class="section-title-wrapper clearfix">
        <h2 class="section-title">{{ name }}</h2>
        <h5 class="section-description">{{ description }}</h5>
      </div>
    </div>

    <div class="row">
      <div class="col-sm-6 col-md-6 col-lg-4 subpage-block">
        <div class="my-photo-block tilt-effect">
          <img :src="photo" alt="">
        </div>
      </div>

      <div class="col-sm-6 col-md-6 col-lg-4">
        <h3>{{ bio_title }}</h3>
        <p>{{ bio_description }}</p>
      </div>

      <div class="col-sm-6 col-md-6 col-lg-4 subpage-block">
        <ul class="info-list">
          <li><span class="title">Age</span><span class="value">{{ age }}</span></li>
          <li><span class="title">Residence</span><span class="value">{{ residence }}</span></li>
          <li><span class="title">Address</span><span class="value">{{ address }}</span></li>
          <li><span class="title">e-mail</span><span class="value"><a :href="'mailto:'+email">{{ email }}</a></span></li>
          <li><span class="title">Phone</span><span class="value">{{ phone }}</span></li>
          <li><span class="title">Freelance</span><span class="value available">{{ freelance }}</span></li>
        </ul>

        <!-- Full list of social icons: http://fontawesome.io/icons/#brand -->
        <ul class="social-links">
          <li v-for="s in social"><a class="tip social-button" :href="s.url" :title="s.name"><i :class="'fa '+s.icon"></i></a></li>
        </ul>
      </div>
    </div>

    <!-- Services block -->
    <div class="block-title">
      <h3>{{ services_name }}</h3>
    </div>

    <div class="row">
      <div class="col-sm-6 col-md-3 subpage-block" v-for="s in services">
        <div class="service-block"> 
          <div class="service-info">
            <img :src="s.icon" :alt="s.name">
            <h4>{{ s.name }}</h4>
            <p>{{ s.description }}</p>
          </div>
        </div>
      </div>
    </div>
    <!-- End of Services block -->

    <!-- Clients block -->
    <div class="block-title">
      <h3>{{ clients_name }}</h3>
    </div>

    <div class="row">
      <div class="col-sm-4 col-md-2 subpage-block" v-for="c in clients">
        <div class="client-block">
          <a :href="c.url" target="_blank"><img :src="c.icon" alt="image"></a>
        </div>
      </div>
    </div>
    <!-- End of Clients block -->

    <div class="row">
      <div class="col-sm-12 col-md-12 subpage-block">
        <div class="block-title">
          <h3>{{ testimonials_name }}</h3>
        </div>
        <div class="testimonials owl-carousel">
          <!-- Testimonial 1 -->
          <div class="testimonial-item" v-for="t in testimonials">
            <!-- Testimonial Content -->
            <div class="testimonial-content">
              <div class="testimonial-text">
                <p>{{ t.text }}</p>
              </div>
            </div>
            <!-- /Testimonial Content -->
            <!-- Testimonial Author -->
            <div class="testimonial-credits">
              <!-- Picture -->
              <div class="testimonial-picture">
                <img :src="t.photo" alt="">
              </div>
              <!-- /Picture -->
              <!-- Testimonial author information -->
              <div class="testimonial-author-info">
                <p class="testimonial-author">{{ t.author }}</p>
                <p class="testimonial-firm">{{ t.firm }}</p>
              </div>
            </div>
            <!-- /Testimonial author information -->
          </div>
          <!-- End of Testimonial 1 -->
        </div>
      </div>
    </div>

    <!-- Fun facts block -->
    <div class="block-title">
      <h3>{{ facts_name }}</h3>
    </div>

    <div class="row">
      <div class="col-sm-6 col-md-3 subpage-block tilt-effect" v-for="(f, i) in facts">
        <div class="fun-fact-block" v-bind:class="i%2 == 0 ? 'gray-bg':''">
          <i :class="'pe-7s-icon '+f.photo"></i>
          <h4>{{ f.text }}</h4>
          <span class="fun-value">{{ f.variable }}</span>
        </div>
      </div>
    </div>
    <!-- End of Fun fucts block -->
  </div>
</section>
<!-- End of About Me Subpage -->
</template>

